@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap');

:root {
    --Pale-blue: hsl(225, 100%, 94%);
    --Bright-blue: hsl(245, 75%, 52%);
    --Very-pale-blue: hsl(225, 100%, 98%);
    --Desaturated-blue: hsl(224, 23%, 55%);
    --Dark-blue: hsl(223, 47%, 23%);
}

.card {
    width: 375px;
    min-height: 500px;
    background-color: rgb(255, 255, 255);
    margin: auto;
    border-radius: 20px;
    text-align: center;
    overflow: hidden;
    box-shadow: 5px 5px 20px #8888;

}

.card-image {
    overflow: hidden;
    width: 100%;
}

.card-image img {
    width: 100%;
}

.card-body {
    padding: 20px 30px;
}

.heading {
    color: var(--Dark-blue);
    font-weight: 900;
}

.desc {
    color: var(--Desaturated-blue);
    font-size: 16px;
}



div#plan {
    background-color: hsl(225, 100%, 98%);
    border-radius: 15px;
    display: flex;
    align-items: center;
    padding: 15px;
    margin: 10px auto;
    font-size: 16px;

}

div#annual-plan {
    display: flex;
    flex-direction: column;
    letter-spacing: 0.5px;
    margin-left: 10px;
}

div#annual-plan span:first-child {
    color: hsl(223, 47%, 23%);
    font-weight: 900;
}

div#annual-plan span:last-child {
    color: hsl(224, 23%, 55%);
    font-weight: 500;
}

span#change {
    font-weight: 500;
    margin-left: 70px;
}

a:hover {
    cursor: pointer;
    color: hsl(251, 94%, 66%);
    text-decoration: none;
}

.payment-btn {
    width: 100%;
    color: white;
    background-color: var(--Bright-blue);
    padding: 15px;
    text-align: center;
    font-weight: 700;
    border-radius: 10px;
    border: none;
    box-shadow: 5px 5px 20px #8888;
    cursor: pointer;
}

.payment-btn:hover {
    background-color: hsl(245, 83%, 68%);
}

.cancel-btn {
    margin: 30px auto;
    font-weight: 700;
    background: none;
    border: none;
    color: var(--Desaturated-blue);
    cursor: pointer;
}

.cancel-btn:hover {
    color: var(--Dark-blue);
}